<template>
  <div v-if="Page.Loaded">
    <div class="LiteLoaderBDS__LatestLL">
      <div class="LiteLoaderBDS__LatestLL-Badge">LATEST</div>
      <h2>LiteLoaderBDS {{ LatestRelease.tag_name }}</h2>
      <details class="LiteLoaderBDS__LatestLL-Changelog" open>
        <summary>Changelog</summary>
        <div
          style="box-sizing: border-box"
          v-html="MD(LatestRelease.body)"
        ></div>
      </details>
      <div class="LiteLoaderBDS__LatestLL-Buttons">
        <div class="LiteLoaderBDS__LatestLL-Spacer"></div>
        <a :href="LatestRelease.html_url" class="LiteLoaderBDS__LatestLL-Button">
          <Icon icon="octicon:download-24" width="20px" /> Download
        </a>
      </div>
    </div>
    <h2 class="LiteLoaderBDS__Download-Header">Other versions</h2>
    <ul class="LiteLoaderBDS__OtherVersions">
      <li
        class="LiteLoaderBDS__OtherVersion"
        v-for="OtherRelease in Releases"
        :key="OtherRelease.key"
      >
        {{ OtherRelease.tag_name }}
        <div>
          <a :href="OtherRelease.html_url">
            <Icon icon="octicon:download-24" width="24px" />
          </a>
        </div>
      </li>
    </ul>
  </div>
  <Icon icon="eos-icons:loading" color="#514a8b" width="36" v-else/>
</template>

<script>
import { Icon } from "@iconify/vue2";

const markdown = require("markdown-it")();

export default {
  components: {
    Icon,
  },

  data() {
    return {
      Page: {
        Loaded: false,
      },
      LatestRelease: null,
      Releases: null,
    };
  },

  methods: {
    MD(src) {
      return markdown.render(src);
    },
  },

  mounted() {
    fetch("https://api.github.com/repos/LiteLDev/LiteLoaderBDS/releases")
      .then((r) => r.json())
      .then((d) => {
        this.LatestRelease = d[0];
        var ReleasesArray = d;
        ReleasesArray.splice(0, 1);
        this.Releases = ReleasesArray;

        this.Page.Loaded = true;
      });
  },
};
</script>

